<template>
    <div class="player" v-show="playlist.length>0">
        <transition name="normal" @enter="enter" @after-enter="afterEnter" @leave="leave" @after-leave="afterLeave">
            <div class="normal-player" v-show="fullScreen">
                <div class="background">
                    <img :src="currentSong.image" width="100%" height="100%">
                </div>
                <div class="top">
                    <div class="back" @click="back">
                        <i class="icon-back"></i>
                    </div>
                    <h1 class="title" v-html="currentSong.name"></h1>
                    <h2 class="subtitle" v-html="currentSong.singer"></h2>
                </div>
                <div class="middle" @touchstart.prevent="middleTouchStart" @touchmove.prevent="middleTouchMove" @touchend="middleTouchEnd">
                    <div class="middle-l" ref="cd">
                        <div class="cd-wrapper" ref="cdWrapper">
                            <div class="cd" :class="cdCls">
                                <img :src="currentSong.image" class="image">
                            </div>
                        </div>
                        <div class="playing-lyric-wrapper">
                          <div class="playing-lyric">{{playingLyric}}</div>
                        </div>
                    </div>
                    <scroll class="middle-r" ref="lyricList" :data="currentLyric && currentLyric.lines">
                      <div class="lyric-wrapper">
                        <div v-if="currentLyric">
                          <p ref="lyricLine" class="text" :class="{'current': currentLineNum === index}" v-for="(line, index) in currentLyric.lines" :key="index">{{line.txt}}</p>
                        </div>
                      </div>
                    </scroll>
                </div>
                <div class="bottom">
                    <div class="dot-wrapper">
                      <span class="dot" :class="{'active': currentShow === 'cd'}"></span>
                      <span class="dot" :class="{'active': currentShow === 'lyric'}"></span>
                    </div>
                    <div class="progress-wrapper">
                      <span class="time time-l">{{formatTime(currentTime)}}</span>
                      <div class="progress-bar-wrapper">
                        <progress-bar :percent="percent" @percentChange="onProgressBarChange"></progress-bar>
                      </div>
                      <span class="time time-r">{{formatTime(currentSong.duration / 1000)}}</span>
                    </div>
                    <div class="operators">
                        <div class="icon i-left" @click="changeMode">
                            <i :class="iconMode"></i>
                        </div>
                        <div class="icon i-left" :class="disableCls">
                            <i @click="prev" class="icon-prev"></i>
                        </div>
                        <div class="icon i-center" :class="disableCls">
                            <i @click="togglePlaying" :class="playIcon"></i>
                        </div>
                        <div class="icon i-right" :class="disableCls">
                            <i @click="next" class="icon-next"></i>
                        </div>
                        <div class="icon i-right">
                            <i class="icon-not-favorite"></i>
                        </div>
                    </div>
                </div>
            </div>
        </transition>
        <transition name="mini">
            <div @click="open" class="mini-player" v-show="!fullScreen">
                <div class="icon">
                    <img :class="cdCls" :src="currentSong.image" width="40" height="40">
                </div>
                <div class="text">
                    <h2 class="name" v-html="currentSong.name"></h2>
                    <p class="desc" v-html="currentSong.singer"></p>
                </div>
                <div class="control">
                    <progress-circle :radius="radius" :percent="percent">
                      <i @click.stop="togglePlaying" class="icon-mini" :class="miniPlayIcon"></i>
                    </progress-circle>
                </div>
                <div class="control">
                    <i class="icon-playlist"></i>
                </div>
            </div>
        </transition>
        <audio ref="audio" :src="currentSong.url" @timeupdate="updateTime" @ended="end" @canplay="ready" @error="error"></audio>
    </div>
</template>

<script>
    import {getSongLyric} from 'api/index'
    import {mapGetters, mapMutations} from 'vuex'
    import animations from 'create-keyframe-animation'
    import {prefixStyle} from 'common/js/dom'
    import {playMode} from 'common/js/config'
    import {shuffle} from 'common/js/util'
    import Scroll from 'base/scroll/scroll'
    import ProgressBar from 'base/progress-bar/progress-bar'
    import ProgressCircle from 'base/progress-circle/progress-circle'
    import Lyric from 'lyric-parser'

    const transform = prefixStyle('transform')
    const transitionDuration = prefixStyle('transitionDuration')

    export default {
        data(){
          return {
            songReady: false,
            currentTime: 0,
            radius: 32,
            currentLyric: null,
            currentLineNum: 0,
            currentShow: 'cd',
            playingLyric: ''
          }
        },
        computed: {
            cdCls(){
              return this.playing ? 'play' : 'play pause';
            },
            iconMode(){
              return this.mode === playMode.sequence ? 'icon-sequence' : this.mode === playMode.loop ? 'icon-loop' : 'icon-random'
            },
            playIcon(){
              return this.playing ? 'icon-pause' : 'icon-play';
            },
            miniPlayIcon(){
              return this.playing ? 'icon-pause-mini' : 'icon-play-mini';
            },
            disableCls(){
              return this.songReady ? '' : 'disable';
            },
            percent(){
              return this.currentTime / this.currentSong.duration
            },
            ...mapGetters([
                'fullScreen',
                'playlist',
                'currentSong',
                'playing',
                'currentIndex',
                'mode',
                'sequenceList'
            ])
        },
        created(){
          this.touch = {}
        },
        components: {
          ProgressBar,
          ProgressCircle,
          Scroll
        },
        methods: {
            togglePlaying(){
              if(!this.songReady){
                return
              }
              this.setPlayingState(!this.playing);
              if(this.currentLyric){
                this.currentLyric.togglePlay()
              }
            },
            back(){
               this.setFullScreen(false)
            },
            open(){
               this.setFullScreen(true)
            },
            prev(){
              if(!this.songReady){
                return
              }
              if(this.playlist.length === 1){
                this.loop()
              }else{
                let index = this.currentIndex - 1;
                if(index === -1){
                  index = this.playlist.length - 1
                }
                this.setCurrentIndex(index);
                if(!this.playing){
                  this.togglePlaying()
                }
              }
              this.songReady = false;
            },
            next(){
              if(!this.songReady){
                return
              }
              if(this.playlist.length === 1){
                this.loop()
              }else{
                let index = this.currentIndex + 1;
                if(index === this.playlist.length){
                  index = 0
                }
                this.setCurrentIndex(index);
                if(!this.playing){
                  this.togglePlaying()
                }
              }
              this.songReady = false;
            },
            loop(){
              this.$refs.audio.currentTime = 0
              this.$refs.audio.play()
              if(this.currentLyric){
                this.currentLyric.seek(0)
              }
            },
            end(){
              if(this.mode === playMode.loop){
                this.loop()
              }else{
                this.next()
              }
            },
            ready(){
              this.songReady = true
            },
            error(){
              this.songReady = true
            },
            enter(el, done){
                const {x, y, scale} = this._getPosAndScale()

                let animation = {
                    0: {
                        transform: `translate3d(${x}px, ${y}px, 0) scale(${scale})`
                    },
                    60: {
                        transform: `translate3d(0, 0, 0) scale(1.1)`
                    },
                    100: {
                        transform: `translate3d(0, 0, 0) scale(1)`
                    }
                }

                animations.registerAnimation({
                    name: 'move',
                    animation,
                    presets: {
                        duration: 500,
                        easing: 'linear'
                    }
                })

                animations.runAnimation(this.$refs.cdWrapper, 'move', done)
            },
            afterEnter(){
                animations.unregisterAnimation('move')
                this.$refs.cdWrapper.style.animation = ''
            },
            leave(el, done){
                this.$refs.cdWrapper.style.transition = 'all 0.5s'
                const {x, y, scale} = this._getPosAndScale()
                this.$refs.cdWrapper.style[transform] = `translate3d(${x}px, ${y}px, 0) scale(${scale})`
                this.$refs.cdWrapper.addEventListener('transitionend', done)
            },
            afterLeave(){
                this.$refs.cdWrapper.style.transition = ''
                this.$refs.cdWrapper.style[transform] = ''
            },
            updateTime(e){
              this.currentTime = e.target.currentTime
            },
            formatTime(interval){
              //此处是将时间戳向下取整，等价于 Math.floor
              interval = interval | 0
              const minute = interval / 60 | 0
              const second = this._pad(interval % 60 | 0)
              return `${minute}:${second}`;
            },
            onProgressBarChange(percent){
              const currentTime = this.currentSong.duration * percent
              this.$refs.audio.currentTime = currentTime / 1000
              if(!this.playing){
                this.togglePlaying()
              }
              if(this.currentLyric){
                this.currentLyric.seek(currentTime)
              }
            },
            changeMode(){
              const mode = (this.mode + 1) % 3
              this.setPlayMode(mode)

              let list = null
              if(mode === playMode.random){
                list = shuffle(this.sequenceList)
              }else{
                list = this.sequenceList
              }
              this._resetCurrentIndex(list)
              this.setPlayList(list)
            },
            middleTouchStart(e){
              this.touch.initiated = true
              this.touch.startX = e.touches[0].pageX
              this.touch.startY = e.touches[0].pageY
            },
            middleTouchMove(e){
              if(!this.touch.initiated){
                return
              }
              const deltaX = e.touches[0].pageX - this.touch.startX
              const deltaY = e.touches[0].pageY - this.touch.startY
              if(Math.abs(deltaY) > Math.abs(deltaX)){
                return
              }
              const left = this.currentShow === 'cd' ? 0 : -window.innerWidth
              const width = Math.min(0, Math.max(-window.innerWidth, left + deltaX))
              this.touch.percent = Math.abs(width / window.innerWidth)
              this.$refs.lyricList.$el.style[transform] = `translate3d(${width}px, 0, 0)`
              this.$refs.lyricList.$el.style[transitionDuration] = 0
              this.$refs.cd.style.opacity = 1 - this.touch.percent
              this.$refs.cd.style[transitionDuration] = 0
            },
            middleTouchEnd(e){
              let width
              let opacity
              if(this.currentShow === 'cd'){
                if(this.touch.percent > 0.1){
                  width = -window.innerWidth
                  opacity = 0
                  this.currentShow = 'lyric'
                }else{
                  width = 0
                  opacity = 1
                }
              }else{
                if(this.touch.percent < 0.9){
                  width = 0
                  opacity = 1
                  this.currentShow = 'cd'
                }else{
                  width = -window.innerWidth
                  opacity = 0
                }
              }
              this.$refs.lyricList.$el.style[transform] = `translate3d(${width}px, 0, 0)`
              this.$refs.lyricList.$el.style[transitionDuration] = '400ms'
              this.$refs.cd.style.opacity = opacity
              this.$refs.cd.style[transitionDuration] = '400ms'
            },
            _resetCurrentIndex(list){
              let index = list.findIndex( item => {
                return item.id === this.currentSong.id
              })
              this.setCurrentIndex(index)
            }, 
            _pad(num, n=2){
              let len = num.toString().length
              while(len<n){
                num = '0' + num
                len++
              }
              return num
            },
            _getPosAndScale(){
                const targetWidth = 40
                const paddingLeft = 40
                const paddingBottom = 30
                const paddingTop = 140
                const width = window.innerWidth * 0.7
                const scale = targetWidth / width
                const x = -(window.innerWidth/2 - paddingLeft)
                const y = window.innerHeight - paddingTop - width/2 - paddingBottom
                return {
                    x, y, scale
                }
            },
            _handleLyric({lineNum, txt}){
              this.currentLineNum = lineNum
              if(lineNum > 6){
                let lineEl = this.$refs.lyricLine[lineNum - 6]
                this.$refs.lyricList.scrollToElement(lineEl, 1000)
              }else{
                this.$refs.lyricList.scrollTo(0, 0, 1000)
              }
              this.playingLyric = txt
            },
            ...mapMutations({
                setFullScreen: 'SET_FULLSCREEN',
                setPlayingState: 'SET_PLAYING_STATE',
                setCurrentIndex: 'SET_CURRENT_INDEX',
                setPlayMode: 'SET_PLAY_MODE',
                setPlayList: 'SET_PLAYLIST'
            })
        },
        watch: {
          currentSong(newSong, oldSong){
            if(newSong.id === oldSong.id){
              return
            }
            if(this.currentLyric){
              this.currentLyric.stop()
            }
            setTimeout(() => {
              this.$refs.audio.play();
              getSongLyric(this.currentSong.id).then(res => {
                this.currentLyric = new Lyric(res.lrc.lyric, this._handleLyric)
                if(this.playing){
                  this.currentLyric.play()
                }
              }).catch(() => {
                this.currentLyric = null
                this.playingLyric = ''
                this.currentNum = 0
              })
            }, 1000)
          },
          playing(newPlaying){
            this.$nextTick(() => {
              const audio = this.$refs.audio
              newPlaying ? audio.play() : audio.pause();
            })
          }
        }
    }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  *
    touch-action: none;
  .player
    .normal-player
      position: fixed
      left: 0
      right: 0
      top: 0
      bottom: 0
      z-index: 150
      background: $color-background-block
      .background
        position: absolute
        left: 0
        top: 0
        width: 100%
        height: 100%
        z-index: -1
        opacity: 0.6
        filter: blur(20px)
      .top
        position: relative
        margin-bottom: 25px
        .back
          position absolute
          top: 6px
          left: 6px
          z-index: 50
          .icon-back
            display: block
            padding: 9px
            font-size: $font-size-large-x
            color: $color-theme
            transform: rotate(-90deg)
        .title
          width: 70%
          margin: 0 auto
          padding-top: 24px
          line-height: 40px
          text-align: center
          no-wrap()
          font-size: $font-size-large
          color: $color-white
        .subtitle
          line-height: 20px
          text-align: center
          font-size: $font-size-medium
          color: $color-white
      .middle
        position: fixed
        width: 100%
        top: 110px
        bottom: 200px
        white-space: nowrap
        font-size: 0
        .middle-l
          display: inline-block
          vertical-align: top
          position: relative
          width: 100%
          height: 0
          padding-top: 70%
          .cd-wrapper
            position: absolute
            left: 15%
            top: 0
            width: 70%
            height: 100%
            .cd
              width: 100%
              height: 100%
              box-sizing: border-box
              border: 6px solid rgba(255, 255, 255, 0.7)
              border-radius: 50%
              &.play
                animation: rotate 20s linear infinite
              &.pause
                animation-play-state: paused
              .image
                position: absolute
                left: 0
                top: 0
                width: 100%
                height: 100%
                border-radius: 50%

          .playing-lyric-wrapper
            width: 80%
            margin: 48px auto 0 auto
            overflow: hidden
            text-align: center
            .playing-lyric
              height: 20px
              line-height: 20px
              font-size: $font-size-medium
              color: $color-white
        .middle-r
          display: inline-block
          vertical-align: top
          width: 100%
          height: 100%
          overflow: hidden
          .lyric-wrapper
            width: 80%
            margin: 0 auto
            overflow: hidden
            text-align: center
            .text
              line-height: 32px
              color: $color-white
              font-size: $font-size-medium
              &.current
                color: $color-theme
      .bottom
        position: absolute
        bottom: 56px
        width: 100%
        .dot-wrapper
          text-align: center
          font-size: 0
          .dot
            display: inline-block
            vertical-align: middle
            margin: 0 4px
            width: 8px
            height: 8px
            border-radius: 50%
            background: $color-highlight-background
            &.active
              width: 20px
              border-radius: 5px
              background: $color-highlight-background
        .progress-wrapper
          display: flex
          align-items: center
          width: 80%
          margin: 0px auto
          padding: 20px 0
          .time
            color: $color-white
            font-size: $font-size-small
            flex: 0 0 30px
            line-height: 30px
            width: 30px
            &.time-l
              text-align: left
            &.time-r
              text-align: right
          .progress-bar-wrapper
            flex: 1
        .operators
          display: flex
          align-items: center
          .icon
            flex: 1
            color: $color-theme
            &.disable
              color: $color-white
            i
              font-size: 30px
          .i-left
            text-align: right
          .i-center
            padding: 0 20px
            text-align: center
            i
              font-size: 40px
          .i-right
            text-align: left
          .icon-favorite
            color: $color-sub-theme
      &.normal-enter-active, &.normal-leave-active
        transition: all 0.5s
        .top, .bottom
          transition: all 0.5s cubic-bezier(0.86, 0.18, 0.82, 1.32)
      &.normal-enter, &.normal-leave-to
        opacity: 0
        .top
          transform: translate3d(0, -100px, 0)
        .bottom
          transform: translate3d(0, 100px, 0)
    .mini-player
      display: flex
      align-items: center
      position: fixed
      left: 0
      bottom: 0
      z-index: 180
      width: 100%
      height: 60px
      background: $color-highlight-background
      &.mini-enter-active, &.mini-leave-active
        transition: all 0.4s
      &.mini-enter, &.mini-leave-to
        opacity: 0
      .icon
        flex: 0 0 40px
        width: 40px
        padding: 0 10px 0 20px
        img
          border-radius: 50%
          &.play
            animation: rotate 10s linear infinite
          &.pause
            animation-play-state: paused
      .text
        display: flex
        flex-direction: column
        justify-content: center
        flex: 1
        line-height: 20px
        overflow: hidden
        .name
          margin-bottom: 2px
          no-wrap()
          font-size: $font-size-medium
          color: $color-text
        .desc
          no-wrap()
          font-size: $font-size-small
          color: $color-text-d
      .control
        flex: 0 0 30px
        width: 30px
        padding: 0 10px
        .icon-play-mini, .icon-pause-mini, .icon-playlist
          font-size: 30px
          color: $color-theme
        .icon-mini
          font-size: 32px
          position: absolute
          left: 0
          top: 0

  @keyframes rotate
    0%
      transform: rotate(0)
    100%
      transform: rotate(360deg)
</style>


